// =============================================================================
// Version Information
// =============================================================================

/**
 * Get package version
 * Reads from package.json at build time
 */
export function getPackageVersion(): string {
  // In Next.js, we can't directly import package.json in all contexts
  // Use environment variable or return a constant
  return process.env.NEXT_PUBLIC_APP_VERSION || process.env.npm_package_version || '0.1.0';
}

