import { NextRequest, NextResponse } from 'next/server';
import { getCDPFacilitator } from '@/integrations/x402/cdp-facilitator';
import { getOrCreateRequestId, createRequestLogger } from '@/lib/request-id';
import { addRequestIdToResponse } from '@/lib/request-id';
import { getDb } from '@/db/client';
import { getPackageVersion } from '@/lib/version';

/**
 * GET /api/x402/health
 * 
 * Comprehensive health check endpoint
 * Checks: database, CDP facilitator, rate limiter
 */
export async function GET(request: NextRequest) {
  const healthCheckStart = Date.now();
  const requestId = getOrCreateRequestId(request);
  const logger = createRequestLogger(requestId, { 
    operation: 'health_check',
    method: 'GET',
    endpoint: '/api/x402/health',
  });
  
  // Latency thresholds (configurable via env)
  const LATENCY_THRESHOLDS = {
    db: {
      warn: parseInt(process.env.HEALTH_DB_WARN_MS || '100', 10), // 100ms warning
      fail: parseInt(process.env.HEALTH_DB_FAIL_MS || '500', 10), // 500ms failure
    },
    cdp: {
      warn: parseInt(process.env.HEALTH_CDP_WARN_MS || '200', 10), // 200ms warning
      fail: parseInt(process.env.HEALTH_CDP_FAIL_MS || '1000', 10), // 1s failure
    },
    rateLimiter: {
      warn: parseInt(process.env.HEALTH_RATE_LIMITER_WARN_MS || '10', 10), // 10ms warning
      fail: parseInt(process.env.HEALTH_RATE_LIMITER_FAIL_MS || '50', 10), // 50ms failure
    },
  };
  
  const checks: Record<string, { 
    status: 'ok' | 'warn' | 'error'; 
    latencyMs?: number; 
    error?: string;
    threshold?: { warn: number; fail: number };
  }> = {};
  let overallHealthy = true;
  
  // Check database
  try {
    const dbStart = Date.now();
    const db = getDb();
    const dbHealthy = await db.healthCheck();
    const dbLatency = Date.now() - dbStart;
    
    // Determine status based on health and latency
    let dbStatus: 'ok' | 'warn' | 'error' = 'ok';
    if (!dbHealthy) {
      dbStatus = 'error';
      overallHealthy = false;
    } else if (dbLatency >= LATENCY_THRESHOLDS.db.fail) {
      dbStatus = 'error';
      overallHealthy = false;
    } else if (dbLatency >= LATENCY_THRESHOLDS.db.warn) {
      dbStatus = 'warn';
    }
    
    checks.db = {
      status: dbStatus,
      latencyMs: dbLatency,
      threshold: LATENCY_THRESHOLDS.db,
    };
    
    if (!dbHealthy) {
      checks.db.error = 'Database health check failed';
    }
    
    logger.debug('Database health check', { 
      component: 'db',
      status: checks.db.status,
      latencyMs: dbLatency,
    });
  } catch (error) {
    overallHealthy = false;
    checks.db = {
      status: 'error',
      error: error instanceof Error ? error.message : 'Unknown database error',
      threshold: LATENCY_THRESHOLDS.db,
    };
    logger.error('Database health check failed', { 
      component: 'db',
      error,
    });
  }
  
  // Check CDP facilitator
  try {
    const cdpStart = Date.now();
    const cdp = getCDPFacilitator();
    const cdpHealth = await cdp.healthCheck();
    const cdpLatency = Date.now() - cdpStart;
    
    // Determine status based on health and latency
    let cdpStatus: 'ok' | 'warn' | 'error' = 'ok';
    if (!cdpHealth.healthy) {
      cdpStatus = 'error';
      overallHealthy = false;
    } else if (cdpLatency >= LATENCY_THRESHOLDS.cdp.fail) {
      cdpStatus = 'error';
      overallHealthy = false;
    } else if (cdpLatency >= LATENCY_THRESHOLDS.cdp.warn) {
      cdpStatus = 'warn';
    }
    
    checks.cdp = {
      status: cdpStatus,
      latencyMs: cdpLatency,
      threshold: LATENCY_THRESHOLDS.cdp,
    };
    
    if (!cdpHealth.healthy) {
      checks.cdp.error = cdpHealth.error || 'CDP health check failed';
    }
    
    logger.debug('CDP health check', { 
      component: 'cdp',
      status: checks.cdp.status,
      latencyMs: cdpLatency,
    });
  } catch (error) {
    overallHealthy = false;
    checks.cdp = {
      status: 'error',
      error: error instanceof Error ? error.message : 'Unknown CDP error',
      threshold: LATENCY_THRESHOLDS.cdp,
    };
    logger.error('CDP health check failed', { 
      component: 'cdp',
      error,
    });
  }
  
  // Check rate limiter (in-memory store)
  try {
    const rateLimitStart = Date.now();
    // Simple check: verify the rate limit store is accessible
    // In a real implementation, you might check Redis connection here
    const rateLimitLatency = Date.now() - rateLimitStart;
    
    // Determine status based on latency
    let rateLimiterStatus: 'ok' | 'warn' | 'error' = 'ok';
    if (rateLimitLatency >= LATENCY_THRESHOLDS.rateLimiter.fail) {
      rateLimiterStatus = 'error';
      overallHealthy = false;
    } else if (rateLimitLatency >= LATENCY_THRESHOLDS.rateLimiter.warn) {
      rateLimiterStatus = 'warn';
    }
    
    checks.rateLimiter = {
      status: rateLimiterStatus,
      latencyMs: rateLimitLatency,
      threshold: LATENCY_THRESHOLDS.rateLimiter,
    };
    
    logger.debug('Rate limiter health check', { 
      component: 'rateLimiter',
      status: checks.rateLimiter.status,
      latencyMs: rateLimitLatency,
    });
  } catch (error) {
    overallHealthy = false;
    checks.rateLimiter = {
      status: 'error',
      error: error instanceof Error ? error.message : 'Unknown rate limiter error',
      threshold: LATENCY_THRESHOLDS.rateLimiter,
    };
    logger.error('Rate limiter health check failed', { 
      component: 'rateLimiter',
      error,
    });
  }
  
  // Calculate total health check response time
  const responseTime = Date.now() - healthCheckStart;
  
  // Log overall health status
  logger.info('Health check completed', { 
    healthy: overallHealthy,
    checks,
    responseTime,
  });
  
  // Get additional system info
  const systemInfo = {
    nodeVersion: process.version,
    platform: process.platform,
    uptime: process.uptime(),
    memory: {
      used: Math.round(process.memoryUsage().heapUsed / 1024 / 1024), // MB
      total: Math.round(process.memoryUsage().heapTotal / 1024 / 1024), // MB
      rss: Math.round(process.memoryUsage().rss / 1024 / 1024), // MB
    },
  };
  
  const response = NextResponse.json({
    status: overallHealthy ? 'ok' : 'error',
    checks,
    version: getPackageVersion(),
    timestamp: new Date().toISOString(),
    system: systemInfo,
    environment: process.env.NODE_ENV || 'development',
    responseTime, // Response time for the health endpoint itself (for SLO monitoring)
  }, {
    status: overallHealthy ? 200 : 503,
  });
  
  addRequestIdToResponse(response, requestId);
  return response;
}

